.app__header {
    position: relative;
    background-color: #18171c;
    min-height: 100vh;
}

.app__header-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../../assets/overlay__bg.png);
    background-position: center;
    background-size: cover;
}

.app__header .app__wrapper_info {
    z-index: 1;
    row-gap: 2rem;
}

.app__header h1:nth-child(2) {
    font-family: var(--font-base);
    font-size: 50px;
    color: var(--color-white);
}

.app__header h1 span {
    color: #743ad5;
}

.app__wrapper_info-desc {
    font-size: 18px;
    color: var(--color-text);
}

.app__wrapper_info-social {
    margin-top: 1rem;
}

.app__wrapper_info-social a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-color: var(--color-dark);
    box-shadow: 2px 0px 2px var(--color-white);
    margin-right: .5rem;
    position: relative;
}

.app__wrapper_info-social a::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-golden);
    transform-origin: center;
    transform: scale(0);
    transition: transform .4s ease;
    z-index: 1;
    border-radius: 100%;
}

.app__wrapper_info-social a:hover:before {
    transform: scale(1);
}

.app__wrapper_info-social a svg {
    font-size: 1.5rem;
    color: var(--color-white);
    cursor: pointer;
    transition: color .4s;
    z-index: 5;
}

.app__wrapper_info-social a:hover {
    box-shadow: 2px 0px 2px var(--color-dark);
}

.app__wrapper_info-social a:hover svg {
    color: var(--color-black);
}


