.app__wrapper_info-buttons button {
    background-color: var(--color-secondary);
    color: var(--color-black);
}

.app__wrapper_info-buttons button:hover {
    background-color: var(--color-primary);
    color: var(--color-white);
}

.app__wrapper_info-buttons a {
    font-family: var(--font-alt);
    margin-left: 1rem;
    color: var(--color-white);
    font-size: 18px;
    transition: all .5s;
}

.app__wrapper_info-buttons a svg {
    margin-right: .5rem;
    transition: all .5s;
}

.app__wrapper_info-buttons a:hover svg,
.app__wrapper_info-buttons a:hover {
    color: var(--color-primary);
}

@media screen and (max-width:1023px) {
    .app__header {
        flex-direction: column-reverse;
    }
    .app__header .app__wrapper_img {
        margin: 0 0 3rem 0;
    }
}