.section__padding {
    padding: 4rem 6rem;
}

.flex__center {
    display: flex;
    justify-content: center;
    align-items: center;
}


.app__bg {
    background: url("./assets/bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    background-attachment: fixed;
}

.app__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
}

.app__wrapper_info {
    flex: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.app__wrapper_img {
    flex: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.app__wrapper_img-reverse {
    justify-content: flex-start;
    margin-right: 2rem;
}

.app__wrapper_img img {
    width: 80%;
}


.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: transparent;
}

.gradient__bg {
    /* background-image: linear-gradient(to right bottom, #6d5893, #634f91, #57458e, #4b3c8c, #3c348a, #2c3081, #1b2b78, #00276f, #00235b, #001e47, #001833, #02101f); */
    /* background: radial-gradient(circle at 3% 25%, #002853 0%, #040c18 100%); */
    background: linear-gradient(267deg, rgba(83,12,181,1) 0%, rgba(131,12,12,1) 100%); 
}


.custom__button {
    all: unset;
    color: var(--color-black);
    font-family: var(--font-base);
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 28px;
    font-size: 20px;
    padding: 1rem 2rem;
    background-color:var(--color-primary);
    /* background-image: linear-gradient(to left top, #0c2744, #3d4f68, #6c7a8e, #9ea9b6, #d3d9e0); */
    color: var(--color-white);
    cursor: pointer;
    border-radius: 10px;
    transition: all ease-in 0.5s;
}

.custom__button:hover {
    background-color: var(--color-secondary);
    color: var(--color-black);
}


.p__cormorant {
    font-family: var(--font-base);
    color: var(--color-white);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-feature-settings: "tnum" on, "lnum" on;
    line-height: 29.9px;
    font-size: 23px;
}

.p__opensans {
    font-family: var(--font-alt);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 28px;
    font-size: 18px;
}

.headtext__cormorant {
    font-family: var(--font-base);
    color: var(--color-golden);
    font-size: 80px;
    line-height: 130%;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}


@media screen and (min-width: 2000px) {

    .custom__button,
    .p__cormorant {
        font-size: 37px;
        line-height: 67px;
    }

    .p__opensans {
        font-size: 30px;
        line-height: 50px;
    }

    .headtext__cormorant {
        font-size: 150px;
        line-height: 210px;
    }


}

@media screen and (max-width: 1150px) {
    .app__wrapper {
        flex-direction: column;
    }

    .app__wrapper_img {
        margin: 5rem 0 0 0;
    }

    .app__wrapper_img-reverse {
        margin: 0 0 5rem 0;
    }

    .app__wrapper_img img {
        width: 100%;
    }
}

@media screen and (max-width: 850px) {
    .section__padding {
        padding: 4rem;
    }
}

@media screen and (max-width: 650px) {
    .section__padding {
        padding: 4rem 2rem;
    }

    .p__cormorant {
        font-size: 21px;
    }

    .p__opensans {
        font-size: 14px;
    }
}

@media screen and (max-width: 450px) {
    .p__opensans {
        font-size: 12px;
    }

    .p__cormorant {
        font-size: 19px;
    }

    .headtext__cormorant {
        font-size: 45px;
        line-height: 70px;
    }
}