.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    padding: 1rem 2rem;
    position: fixed;
    top: 0;
    z-index: 1000;
}

.bg-header {
    box-shadow: 0px 0px 8px rgba(255, 255, 255, 0.3);
     background-color:var(--color-dark) ;
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo img {
    width: 150px;
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
    transition: 0.5s ease;
}

.app__navbar-links li:hover {
    color: var(--color-grey);
}

.app__navbar-data {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.app__navbar-data a {
    margin: 0 1rem;
    transition: 0.5s ease;
}




