.app__navbar-data_social a {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 1px solid var(--color-golden);
    border-radius: 50%;
    margin-left: 0.5rem;
}

.app__navbar-data_social svg {
    font-size: 1.5rem;
    color: var(--color-white);
}

.app__navbar-data_social a::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    border-radius: 50px;
    z-index: -1;
    transform-origin: center;
    transform: scale(0);
    transition: transform .4s ease;
}

.app__navbar-data_social a:hover:before {
    transform: scale(1);
}

.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscren_overlay {
    position: fixed;
    top: 0rem;
    right: 0;
    width: 40vw;
    height: 100vh;
    background: var(--gradient-bar);
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen .close {
    position: fixed;
    z-index: 10;
}

.app__navbar-smallscren-links li {
    font-family: var(--font-base);
    font-weight: normal;
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    text-align: center;
}

.app__navbar-smallscren-links li a {
    position: relative;
}

.app__navbar-smallscren-links li a::before {
    height: 2px;
}

@media screen and (width >=2000px) {
    .app__navbar-logo img {
        width: 220px;
    }
}

@media screen and (width <=1150px) {
    .app__navbar-links {
        display: none;
    }

    .app__navbar-data {
        display: none;
    }

    .app__navbar-smallscreen {
        display: block;
    }

    .app__navbar-smallscreen svg {
        cursor: pointer;
    }

    .app__navbar-smallscreen .close {
        top: 3%;
        right: 6%;

    }

    .app__navbar-smallscren_overlay .custom__button {
        margin: 2rem;
    }
}

@media screen and (width <=650px) {
    .app__navbar-smallscren_overlay {
        width: 100vw;
        height: 40vh;
    }

    .app__navbar {
        padding: 1rem;
    }

    .app__navbar-logo img {
        width: 110px;
    }
}