.app__wrapper_info h1 {
    font-size: 40px;
    font-weight: 700;
    font-family: var(--font-alt);
    margin-bottom: 2rem;
}

.app__wrapper_info form {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.app__wrapper_info form .label {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
}

.app__wrapper_info form .label input {
    all: unset;
    width: 100%;
    padding: 1rem;
    background: transparent;
    border: 1px solid var(--color-grey);
    border-radius: 10px;
    color: var(--color-white);
    transition: background .4s ease;
}

.app__wrapper_info form textarea::placeholder,
.app__wrapper_info form .label input::placeholder {
    color: var(--color-white);
    font-family: var(--font-base);
}

.app__wrapper_info form textarea:focus,
.app__wrapper_info form .label input:focus {
    background-color: var(--color-black);
    transition: background .4s ease;
}

.app__wrapper_info form textarea {
    background: transparent;
    outline: 0;
    font-family: var(--font-base);
    padding: 1rem;
    color: var(--color-white);
    border-radius: 10px;
}

.app__wrapper_info form button {
    width: fit-content;
    overflow: hidden;
    padding: .8rem 1.5rem;
    margin-top: 1rem;
    background-color: #f4f4f4;
    color: var(--color-black);
    font-size: 16px;
    font-weight: 600;
    border: none;
    cursor: pointer;
    position: relative;
    transition: background .4s ease, color .4s ease;
}



