.app__footersocial {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-content: center;
}
.app__footersocial-logo{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:660px) {
    .app__footersocial {
        flex-direction: column;
        justify-content: center;
        gap: 20px;
    }
}