.app__footerinfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateY(-70%);
    background-image: linear-gradient(to right bottom, #6d5893, #634f91, #57458e, #4b3c8c, #3c348a, #2c3081, #1b2b78, #00276f, #00235b, #001e47, #001833, #02101f);
    padding: 2rem 4rem;
    width: 90%;
    border-radius: 10px;
}

.app__newsletter-heading {
    text-align: center;
}

.app__newsletter-heading h1 {
    font-size: 50px;
}

.app__newsletter-input {
    width: 100%;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-white);
    /* margin-right: 2rem; */
    padding: 0.75rem 1rem;
    background: transparent;
    flex-direction: row;
    margin-top: 3rem;
}

.app__newsletter-input input {
    all: unset;
    width: 100%;

    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-white);
    margin-right: 2rem;
    padding: 0.75rem 1rem;
    background: transparent;
}

.app__newsletter-input button {
    width: max-content;
}

@media screen and (width >=2000px) {
    .app__newsletter-input input {
        font-size: 2rem;
    }
}

@media screen and (width <=990px) {
    .app__newsletter-input {
        flex-direction: row;
        width: 100%;
    }

    .app__newsletter-input input {
        /* margin: 0 0 2rem 0; */
        width: 100%;
    }

    .app__newsletter-heading h1 {
        font-size: 30px;
    }

}

@media screen and (width <=650px) {

    .app__newsletter {
        padding: 2rem 2rem;
        border: none;
    }

    .app__footerinfo {
        padding: 1rem 2rem;
    }

    .app__newsletter-input {
        flex-direction: column;
        width: 100%;
        border: none;
        margin-top: 1rem;
    }

    .app__newsletter-input input {
        border: 1px solid var(--color-golden);
        border-radius: 5px;
        margin:0 0 1rem 0;
    }
    .app__newsletter-input button{
        padding: .5rem 1.2rem;
    }
}

@media screen and (width <=300px) {
    .app__newsletter-heading h1 {
        font-size: 32px;
        line-height: 50px;
    }
}