.app__skills-heading {
    text-align: center;
    margin-bottom: 2rem;
}

:root {
    --dot-diameter: 200px;
    --circle-border-width: 2px;
    --default-color: gainsboro;
}
.app__skills-carousel{
    padding: 2rem 0;
    border-radius: 20px;
}
.app__skills-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.app__skills-card_per {
    font-family: var(--font-base);
    font-weight: 700;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--color-secondary);
    font-size: 2.5rem;
}

.circle-container {
    width: var(--dot-diameter);
    height: var(--dot-diameter);
    transform: rotate(-90deg);
}

.circle-container__background {
    fill: none;
    stroke: var(--default-color);
    stroke-width: var(--circle-border-width);
}

.circle-container__progress {
    fill: none;
    stroke-linecap: round;
    stroke: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
    stroke-dasharray: 100 100;
    stroke-linecap: round;
    stroke-width: var(--circle-border-width);
    transition: stroke-dashoffset 1s ease-in-out;
    will-change: transform;
}

.app__skills-card-skill {
    font-family: var(--font-base);
    font-size: 24px;
    color: var(--color-white);
    margin-top: 1rem;
}
.owl-dot{
    margin-top: 1rem;
}
.owl-dot span {
    width: 16px !important;
    height: 16px !important;
    margin: 7px 9px !important;
    background: var(--color-primary) !important;
    position: relative;
}

.owl-dot span:before {
    position: absolute;
    content: "";
    top: 1px;
    bottom: 1px;
    left: 1px;
    right: 1px;
    border-radius: 100%;
    border: 4px solid var(--color-secondary);
    z-index: -1;
    transition: all .3s ease;
}

.owl-dot.active span:before {
    top: -5px;
    bottom: -5px;
    left: -5px;
    right: -5px;
}
@media screen and (max-width:600px) {
    :root {
        --dot-diameter: 120px;
    } 
    .app__skills-card-skill {
        font-size: 20px;
}}