.app__projects-heading {
    text-align: center;
    margin-bottom: 2rem;
}

.app__projects-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.app__projects-info_route {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__projects-info_route a {
    overflow: hidden;
    position: relative;
    font-size: 22px;
    color: var(--color-secondary);
    padding: .8rem 3rem;
    border: 2px solid var(--color-secondary);
    z-index: 1;
}

.app__projects-info_route a:nth-child(1)::before,
.app__projects-info_route a:nth-child(1) {
    border-radius: 40px 0 0 40px;
}

.app__projects-info_route a:nth-child(3)::before,
.app__projects-info_route a:nth-child(3) {
    border-radius: 0 40px 40px 0;
}

.app__projects-info_route a::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    background: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);
    transition: all .4s ease;
    z-index: -1;
}

.app__projects-info_route a:hover::before {
    left: 0;
}

.app__projects-info_route a.active {
    background: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

}

.app__projects-info_content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2rem;
    text-align: center;
    gap: 1rem;
}

.app__projects-info_projectcard {
    display: flex;
    flex-direction: column;
    gap: 15px;
    text-align: left;
}

.app__projects-info_projectcard img {
    width: 320px;
    border-radius: 20px;
}

.app__projects-info_projectcard h3 {
    font-size: 30px;
    color: var(--color-primary);
}

@media screen and (max-width:660px) {
    .app__projects-info_route a {
        font-size: 18px;
        padding: .8rem 1.5rem;
    }
}